        deleteDialog:{
            cancel: "Annuleren",
            confirm: "Ja",
            header:"API-sleutel verwijderen",
            message: x=>`Weet je zeker dat je wilt verwijderen ${x}`,

        },
        types:["Facturen", "Offertes", "Uren"]
